import React from 'react';
import { Box, VStack } from '@chakra-ui/react';
import { Link } from 'react-router-dom';

function Nav() {
  const click = (anchor) => {
    const id = anchor.href.split('#')[1];
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  };
  const menuButtons = ["Home", "About", "Menu", "Contact"]
  return (
      <VStack
      color="#fff"
      justify="center"
      fontFamily="DragonSerial"
      fontSize="clamp(1rem, 8vw, 5rem)"
      lineHeight="normal"
      align="start"
      >
        {menuButtons.map ((element) => (<Box 
        key={element} 
        as={Link} 
        to={"./#" + element.replace(/\s/g, '').toLowerCase()}
        onClick={(e) => click(e.target)} 
        _hover={
          {
          textDecor:"none", color: "gray.400"}
        }>
          {element}
        </Box>
))}
      </VStack>
  )
}

export default Nav